.mainWrapper {
  position: relative;
  display: flex;
  padding: 30px;
  color: #6db8ae;
}
.wrapper {
  margin-left: 30px;
  width: 40%;
}
.mainWrapper::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 3px;
  background-color: darkgoldenrod;
}
.title {
  color: aquamarine;
}
.secondaryTitle {
  color: rgb(141, 248, 212);
}
